import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styles from './Tag.css';
import theme from '../common/theme';

export const TagVariants = {
	active: 'active',
	search: 'search'
};

@theme(styles)
export class Tag extends Component {
	render() {
		const { css = styles, variant, onClick, label } = this.props;
		return (
			<a className={`${css.root} ${variant ? css[variant] : ''}`} onClick={onClick}>
				{label}
			</a>
		);
	}
}

export default Tag;

Tag.propTypes = {
	/**
	 * Label to show in the tag
	 */
	label: PropTypes.string,

	/**
	 * Called when this button is clicked
	 */
	onClick: PropTypes.func,

	/**
	 * State in which this avatar should display
	 */
	variant: PropTypes.oneOf(Object.keys(TagVariants))
};



