import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import theme from '../common/theme';
import styles from './Heading.css';
import Truncate from '../truncate/Truncate';

@theme(styles)
export class Heading extends Component {
	render() {
		const { css = styles, children, headingClass = 'h2', level = 2, lines = { xsmall: 3 } } = this.props;
		const headingLevel = Math.trunc(Math.abs(level));
		const Tag = `h${Math.min(headingLevel, 6)}`;
		return (
			<Fragment>
				{children && (
					<Truncate lines={lines}>
						<Tag className={`orm-${headingClass} ${css.root}`}>{children}</Tag>
					</Truncate>
				)}
			</Fragment>
		);
	}
}

export default Heading;

Heading.propTypes = {
	/**
	 * Children
	 */
	children: PropTypes.node,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Heading CSS class to be used in heading element
	 */
	headingClass: PropTypes.string,

	/**
	 * Heading level to be used in heading element
	 */
	level: PropTypes.oneOf([1, 2, 3, 4, 5, 6]),

	/**
	 * How many lines the heading should show brfore truncating
	 */
	lines: PropTypes.object
};



