import React, { Component } from 'react';
import PropTypes from 'prop-types';

import AnonymousFooter from '../anonymousfooter/AnonymousFooter';
import AnonymousNavigation from '../anonymousnavigation/AnonymousNavigation';
import Footer from '../footer/Footer';
import Navigation from '../navigation/Navigation';

import styles from './Chrome.css';
import theme from '../common/theme';

const SCROLL_THRESHOLD = 5;
const SEARCH_PATH = '/search/';

@theme(styles)
export class Chrome extends Component {
	constructor(props) {
		super(props);

		this._onScroll = this._onScroll.bind(this);
		this.search = this.search.bind(this);

		this.state = { scrolled: false };
	}

	async componentDidMount() {
		await this._onScroll();
	}

	/* istanbul ignore next */
	_onScroll(event) {
		return this.setState({
			scrolled: event ? event.target.scrollTop > SCROLL_THRESHOLD : false
		});
	}

	/* istanbul ignore next */
	search(query, filter) {
		const { onSearch } = this.props;

		if (onSearch) {
			onSearch(query, filter);
		} else {
			window.location.href = `${window.location.origin}${SEARCH_PATH}?query=${query}&field=${filter}`;
		}
	}

	render() {
		const {
			css = styles,
			children,
			headerLinks,
			secondaryHeaderLinks,
			footerLinks,
			onAutocomplete,
			onAutocompleteClick,
			user: { user_identifier } = {}
		} = this.props;
		const { scrolled } = this.state;

		return (
			<div className={`${css.root} ${user_identifier ? css.authed : css.anon} ${scrolled ? css.scrolled : ''}`}>
				<div className={css.navComponents}>
					{!user_identifier && (
						<AnonymousNavigation extraClasses={{ root: css.anonNav }} onSearch={this.search} />
					)}
					{user_identifier && (
						<Navigation
							extraClasses={{ root: css.nav }}
							items={headerLinks}
							onSearch={this.search}
							onAutocomplete={onAutocomplete}
							onAutocompleteClick={onAutocompleteClick}
							secondaryItems={secondaryHeaderLinks}
							slim={scrolled}
						/>
					)}
				</div>
				<div className={css.content} onScroll={this._onScroll}>
					{children}
					<div className={css.footerComponents}>
						{!user_identifier && <AnonymousFooter extraClasses={{ root: css.anonFooter }} />}
						{user_identifier && <Footer extraClasses={{ root: css.footer }} items={footerLinks} />}
					</div>
				</div>
			</div>
		);
	}
}

export default Chrome;

Chrome.propTypes = {
	/**
	 * Content displayed inside this item
	 */
	children: PropTypes.node,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Links displayed in the authenticated footer bar
	 */
	footerLinks: PropTypes.node,

	/**
	 * Custom links displayed in the authenticated navigation bar
	 */
	headerLinks: PropTypes.node,

	/**
	 * Content to show in a dismissable application-level notification
	 */
	notificationContent: PropTypes.node,

	/**
	 * A callback function fired as the user enters text
	 */
	onAutocomplete: PropTypes.func,

	/**
	 * A callback function fired as the user clicks an autocomplete item
	 */
	onAutocompleteClick: PropTypes.func,

	/**
	 * Called with the current query when a user triggers a search
	 */
	onSearch: PropTypes.func,

	/**
	 * Custom secondary links displayed in the authenticated navigation bar
	 */
	secondaryHeaderLinks: PropTypes.node,

	/**
	 * Authenticated user object
	 */
	user: PropTypes.object
};



