import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import Icon from '../icon/Icon';
import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';
import { uid } from '../common/util';

import styles from './Checkbox.css';

export const CheckboxSizes = {
	small: 'small',
	large: 'large'
};

@responsive(['size'])
@theme(styles)
export class Checkbox extends Component {
	constructor(props) {
		super(props);
		this._id = uid('checkbox');
		this.handleOnChange = this.handleOnChange.bind(this);
	}

	handleOnChange(event) {
		const { name, checked } = event.target;
		const { value = true, onChange } = this.props;
		const returnValue = checked ? value : false;
		onChange && onChange(event, name, returnValue);
	}

	render() {
		const { css = styles, checked, disabled, id = this._id, label = 'label', name, value, size } = this.props;
		return (
			<div
				className={classnames(css.root, {
					[css[size]]: size,
					[css.checked]: checked,
					[css.disabled]: disabled
				})}>
				<input
					className={css.input}
					checked={checked}
					disabled={disabled}
					id={id}
					name={name}
					onChange={this.handleOnChange}
					type="checkbox"
					value={value}
				/>
				<div className={css.checkbox}>
					{checked && <Icon extraClasses={{ icon: css.icon }} name="checkmark" />}
				</div>
				{label && (
					<label className={css.label} htmlFor={id}>
						{label}
					</label>
				)}
			</div>
		);
	}
}

export default Checkbox;

Checkbox.propTypes = {
	/**
	 * Whether the checkbox is currently checked
	 */
	checked: PropTypes.bool,

	/**
	 * Determines if this checkbox is disabled
	 */
	disabled: PropTypes.bool,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * String id to set on this checkbox, useful for labeling
	 */
	id: PropTypes.string,

	/**
	 * Label for the checkbox
	 */
	label: PropTypes.string.isRequired,

	/**
	 * Name to apply to the underlying checkbox
	 */
	name: PropTypes.string,

	/**
	 * Called when this checkbox is clicked
	 */
	onChange: PropTypes.func,

	/**
	 * Size of input
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.keys(CheckboxSizes))),

	/**
	 * Value to apply to the underlying checkbox
	 */
	value: PropTypes.string
};



