import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import Icon from '../icon/Icon';
import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';
import { uid } from '../common/util';

import styles from './Select.css';

export const SelectSizes = {
	small: 'small',
	large: 'large'
};

@responsive(['size'])
@theme(styles)
export class Select extends Component {
	constructor(props) {
		super(props);
		this.state = { focus: false };
		this._id = uid('select');
		this.handleOnChange = this.handleOnChange.bind(this);
		this.handleOnFocus = this.handleOnFocus.bind(this);
		this.handleOnBlur = this.handleOnBlur.bind(this);
	}

	handleOnChange(e) {
		const { onChange } = this.props;
		onChange && onChange(e, e.target.name, e.target.value);
	}

	handleOnFocus() {
		this.setState({ focus: true });
	}

	handleOnBlur() {
		this.setState({ focus: false });
	}

	render() {
		const {
			children,
			css = styles,
			disabled,
			error,
			id = this._id,
			invalid = false,
			label,
			name,
			placeholder,
			required = false,
			size,
			value
		} = this.props;

		const rootClasses = classnames(css.root, css[size], {
			[css.focus]: this.state.focus,
			[css.withLabel]: label,
			[css.invalid]: invalid,
			[css.disabled]: disabled
		});
		const unset = typeof value === 'undefined';
		const iconSize = size == SelectSizes.small ? 8 : 16;

		return (
			<Fragment>
				<div className={rootClasses}>
					{label && (
						<label className={css.selectLabel} htmlFor={id}>
							<span>{`${label}:`}</span>
						</label>
					)}
					<select
						id={id}
						className={css.select}
						disabled={disabled}
						onChange={this.handleOnChange}
						onFocus={this.handleOnFocus}
						onBlur={this.handleOnBlur}
						name={name}
						defaultValue={placeholder}
						value={value}
						aria-required={required}
						aria-describedby={`error-${id}`}
						aria-invalid={invalid}>
						<option disabled>{placeholder}</option>
						{children}
					</select>
					<Icon
						name={unset ? 'chevron-down' : 'chevron-up'}
						size={iconSize}
						extraClasses={{ icon: css.icon }}
					/>
				</div>
				{invalid &&
					error && (
						<div className={css.errorWrapper}>
							<Icon
								name="warning-fill"
								extraClasses={{ icon: css.errorIcon }}
								size={iconSize}
								ariaHidden={true}
							/>
							<span id={`error-${id}`} className={css.errorTxt} aria-live="polite">
								{error}
							</span>
						</div>
					)}
			</Fragment>
		);
	}
}

export default Select;

Select.defaultProps = {
	placeholder: 'Please choose...'
};

Select.propTypes = {
	/**
	 * Options to render in this component
	 */
	children: PropTypes.node,

	/**
	 * Determines if this select is disabled
	 */
	disabled: PropTypes.bool,

	/**
	 * Validation error message
	 */
	error: PropTypes.string,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Custom id for the select
	 */
	id: PropTypes.string,

	/**
	 * Determines if this select is invalid
	 */
	invalid: PropTypes.bool,

	/**
	 * Optional label for this components
	 */
	label: PropTypes.string,

	/**
	 * Name of the select
	 */
	name: PropTypes.string,

	/**
	 * Called when this select is changed
	 */
	onChange: PropTypes.func,

	/**
	 * Placeholder to show inside this select
	 */
	placeholder: PropTypes.string,

	/**
	 * Determines if this select is required
	 */
	required: PropTypes.bool,

	/**
	 * Size of select
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.keys(SelectSizes))),

	/**
	 * Value to apply to the underlying select
	 */
	value: PropTypes.string
};



