import React, { Component } from 'react';
import PropTypes from 'prop-types';

import theme from '../common/theme';
import styles from './ProgressIndicator.css';

@theme(styles)
export class ProgressIndicator extends Component {
	render() {
		const { css = styles, total, current, onClick } = this.props;
		const bullets = [];
		for (let i = 1; i <= total; i++) {
			bullets.push(
				<span
					className={`${css.page} ${i == current ? css.active : ''}`}
					key={i}
					onClick={onClick && onClick.bind(this, i)}>
					<span className={css.bullet} />
				</span>
			);
		}
		return <div className={css.root}>{bullets}</div>;
	}
}

export default ProgressIndicator;

ProgressIndicator.propTypes = {
	/**
	 * Total number of pages
	 */
	total: PropTypes.number.isRequired,

	/**
	 * Current page number
	 */
	current: PropTypes.number.isRequired,

	/**
	 * Callback
	 */
	onClick: PropTypes.func,

	/**
	 * Extra classes for the ProgressIndicator component
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string)
};



