import React, { Component } from 'react';
import PropTypes from 'prop-types';
import { Link as RouterLink } from 'react-router-dom';
import Icon from '../icon/Icon';
import theme from '../common/theme';
import styles from './Link.css';

export const LinkVariants = {
	a: 'a',
	link: 'link'
};

@theme(styles)
export class Link extends Component {
	render() {
		const { children, css = styles, onClick, target, to, variant, icon } = this.props;

		if (variant === LinkVariants.a) {
			return (
				<a href={to} onClick={onClick} target={target} className={css.root}>
					{icon && <Icon name={icon} extraClasses={{ root: css.icon }} />}
					{children}
				</a>
			);
		}

		return (
			<RouterLink to={to} onClick={onClick} target={target} className={css.root}>
				{children}
			</RouterLink>
		);
	}
}

export default Link;

Link.defaultProps = {
	target: null,
	to: '#',
	variant: LinkVariants.a
};

Link.propTypes = {
	/**
	 * Content displayed inside the anchor
	 */
	children: PropTypes.node.isRequired,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Optional icon
	 */
	icon: PropTypes.string,

	/**
	 * Called when this anchor is clicked
	 */
	onClick: PropTypes.func,

	/**
	 * The target of the anchor
	 */
	target: PropTypes.oneOf([null, '_blank', '_parent', '_self', '_top']),

	/**
	 * Pathname or location to link to
	 */
	to: PropTypes.string,

	/**
	 * Determines if an HTML <a> or React Router <Link> is created
	 */
	variant: PropTypes.oneOf(Object.keys(LinkVariants))
};



