import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import Icon from '../icon/Icon';
import responsive, { ResponsivePropType } from '../common/responsive';
import styles from './Button.css';
import theme from '../common/theme';

export const ButtonVariants = {
	secondary: 'secondary',
	secondaryReverse: 'secondaryReverse',
	destructive: 'destructive',
	tertiary: 'tertiary'
};

export const ButtonSizes = {
	small: 'small',
	large: 'large'
};

@responsive(['size', 'variant'])
@theme(styles)
export class Button extends Component {
	render() {
		const { css = styles, size, variant, icon, onClick, children, disabled } = this.props;
		const classes = classnames(css.root, css[size], css[variant]);

		return (
			<button className={classes} onClick={onClick} disabled={disabled}>
				<span>{children}</span>
				{icon && (
					<Icon name={icon} extraClasses={{ icon: css.icon }} size={size !== ButtonSizes.small ? 24 : 16} />
				)}
			</button>
		);
	}
}

export default Button;

Button.propTypes = {
	/**
	 * Button label
	 */
	children: PropTypes.node,

	/**
	 * Determines if this button is disabled
	 */
	disabled: PropTypes.bool,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Show an icon in your button. This value should exactly match a name in the Icon component
	 */
	icon: PropTypes.string,

	/**
	 * Called when this button is clicked
	 */
	onClick: PropTypes.func,

	/**
	 * Button size
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.keys(ButtonSizes))),

	/**
	 * Changes the button variant
	 */
	variant: ResponsivePropType(PropTypes.oneOf(Object.keys(ButtonVariants)))
};



