import React, { Component } from 'react';
import PropTypes from 'prop-types';

import theme from '../common/theme';

import styles from './Reminder.css';

@theme(styles)
export class Reminder extends Component {
	render() {
		const { css = styles, user: { username = 'User', user_type: userType = '' } } = this.props;
		const showReminder = ['cancelled', 'trial', 'expired'].some(type => type === userType.toLowerCase());

		if (!showReminder) {
			return null;
		}

		let message;

		switch (userType.toLowerCase()) {
			case 'cancelled':
				message = (
					<div>
						Your membership was cancelled, {username}. Resubscribe today.{' '}
						<a href="/subscribe/">See pricing options.</a>
					</div>
				);
				break;
			case 'expired':
				message = (
					<div>
						Your trial membership has ended, {username}. Subscribe today.{' '}
						<a href="/subscribe/">See pricing options.</a>
					</div>
				);
				break;
			case 'trial':
				message = (
					<div>
						Find answers on the fly, or master something new. <a href="/subscribe/">Subscribe today.</a>
					</div>
				);
				break;
		}

		return <div className={css.root}>{message}</div>;
	}
}

export default Reminder;

Reminder.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Authenticated user object
	 */
	user: PropTypes.object.isRequired
};



