import React, { Component } from 'react';
import PropTypes from 'prop-types';
import theme from '../common/theme';
import styles from './Footer.css';

const YEAR = new Date().getFullYear();

@theme(styles)
export class Footer extends Component {
	render() {
		const { items, css = styles } = this.props;

		return (
			<footer role="contentinfo" className={css.root}>
				<div className={css.links}>
					{items || (
						<ul>
							<li>
								<a href="/">Browse</a>
							</li>
							<li>
								<a href="/r/">Recommended</a>
							</li>
							<li>
								<a href="/s/">Queue</a>
							</li>
							<li>
								<a href="/history/">History</a>
							</li>
							<li>
								<a href="/topics/">Topics</a>
							</li>
							<li>
								<a href="/tutorials/">Tutorials</a>
							</li>
							<li>
								<a href="/u/">Settings</a>
							</li>
							<li>
								<a href="/public/support/">Support</a>
							</li>
							<li>
								<a href="/accounts/logout/">Sign Out</a>
							</li>
						</ul>
					)}
				</div>
				<div className={css.copyright}>
					©{YEAR} Safari.&nbsp;
					<ul>
						<li>
							<a href="https://www.safaribooksonline.com/terms/">Terms of Service</a>
						</li>
						<li>
							<a href="https://www.safaribooksonline.com/privacy/">Privacy Policy</a>
						</li>
					</ul>
				</div>
			</footer>
		);
	}
}

export default Footer;

Footer.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Links to display inside this component
	 */
	items: PropTypes.node
};



