import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';
import throttle from 'lodash.throttle';

import Pagination, { PaginationSizes, PaginationVariants } from '../pagination/Pagination';
import theme from '../common/theme';
import { Grid, Row } from '../grid/Grid';
import { assignValueByBreakpoint } from '../common/responsive';

import styles from './CardGroup.css';

export const CardGroupVariants = {
	carousel: 'carousel'
};

@theme(styles)
export class CardGroup extends Component {
	constructor(props) {
		super(props);

		this.state = Object.assign({ offset: 0 }, this.countPages());
		this.handlePaginationNext = this.handlePaginationNext.bind(this);
		this.handlePaginationPrev = this.handlePaginationPrev.bind(this);
		this.throttledOnResize = throttle(this.onResize.bind(this), 100, {
			leading: false
		});
	}

	countPages() {
		if (this.props.variant != CardGroupVariants.carousel) return {};

		/* istanbul ignore next line */
		const offset = this.state ? this.state.offset : 0;
		const cardsPerPage = assignValueByBreakpoint({
			xsmall: 2,
			small: 3,
			medium: 4
		});
		const totalCards = this.props.children.length;
		return {
			cardsPerPage,
			totalCards,
			hasNextPage: offset + cardsPerPage < totalCards,
			hasPrevPage: offset > 0,
			offset: Math.round(offset / cardsPerPage) * cardsPerPage
		};
	}

	componentDidMount() {
		window.addEventListener('resize', this.throttledOnResize);
	}

	/* istanbul ignore next */
	componentWillUnmount() {
		removeEventListener('resize', this.throttledOnResize);
	}

	/* istanbul ignore next */
	onResize() {
		this.setState(this.countPages());
	}

	handlePaginationNext() {
		const { offset, totalCards, cardsPerPage } = this.state;
		const newOffset = Math.min(totalCards - cardsPerPage, offset + cardsPerPage);
		this.setState({
			offset: newOffset,
			hasNextPage: newOffset + cardsPerPage < totalCards,
			hasPrevPage: newOffset > 0
		});
	}

	handlePaginationPrev() {
		const { offset, totalCards, cardsPerPage } = this.state;
		const newOffset = Math.max(0, offset - cardsPerPage);
		this.setState({
			offset: newOffset,
			hasNextPage: newOffset + cardsPerPage < totalCards,
			hasPrevPage: newOffset > 0
		});
	}

	render() {
		const { css = styles, variant } = this.props;
		const { offset, cardsPerPage, hasPrevPage, hasNextPage } = this.state;
		const isCarousel = variant == CardGroupVariants.carousel;
		const children = isCarousel
			? React.Children.map(this.props.children, (child, index) =>
					React.cloneElement(child, { ariaHidden: index < offset || index >= offset + cardsPerPage })
				)
			: this.props.children;

		return (
			<div className={css.container}>
				<Grid className={css.root} tag="section">
					{isCarousel && (
						<Pagination
							previous
							disabled={!hasPrevPage}
							extraClasses={{ root: `${css.pager} ${css.previous}` }}
							size={PaginationSizes.large}
							variant={PaginationVariants.block}
							onClick={this.handlePaginationPrev}
						/>
					)}
					{isCarousel && (
						<Pagination
							next
							disabled={!hasNextPage}
							extraClasses={{ root: `${css.pager} ${css.next}` }}
							size={PaginationSizes.large}
							variant={PaginationVariants.block}
							onClick={this.handlePaginationNext}
						/>
					)}
					<Row
						style={{ transform: `translateX(-${offset * (100 / cardsPerPage)}%)` }}
						extraClasses={{ root: classnames(css.row, { [css[variant]]: variant }) }}>
						{children}
					</Row>
				</Grid>
			</div>
		);
	}
}

export default CardGroup;

CardGroup.propTypes = {
	/**
	 * Cards to display within this group
	 */
	children: PropTypes.node,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Layout Variant
	 */
	variant: PropTypes.oneOf(Object.keys(CardGroupVariants))
};



