/* eslint react/prop-types: 0 */
import React, { Component } from 'react';
import PropTypes from 'prop-types';
import throttle from 'lodash.throttle';

export const Breakpoints = [
	{ name: 'xsmall', width: 0, active: true },
	{ name: 'xsmall600', width: 600, active: false },
	{ name: 'small', width: 720, active: false },
	{ name: 'small960', width: 960, active: false },
	{ name: 'medium', width: 1280, active: false }
];

export function ResponsivePropType(Type) {
	return PropTypes.oneOfType([
		Type,
		PropTypes.shape({
			xsmall: Type,
			xsmall600: Type,
			small: Type,
			small960: Type,
			medium: Type
		})
	]);
}

function checkBreakpoints() {
	Breakpoints.forEach(breakpoint => {
		breakpoint.active = window.matchMedia(`(min-width: ${breakpoint.width}px)`).matches;
	});
}

// event listener ouside of component lifecycle to run only once per resize.
if (typeof window !== 'undefined') {
	checkBreakpoints();
	window.addEventListener('resize', throttle(checkBreakpoints, 100, { leading: false }));
}

/**
 * Chooses the key of an object based on the currently-active breakpoint
 *
 * @param {object} prop - Hash of breakpoint names to prop value
 */
export function assignValueByBreakpoint(prop) {
	if (typeof prop !== 'object') return prop;
	let newProp = null;
	Breakpoints.forEach(breakpoint => {
		if (breakpoint.active && typeof prop[breakpoint.name] !== 'undefined') {
			newProp = prop[breakpoint.name];
		}
	});
	return newProp;
}

/**
 * ES class decorator to apply props based on media queries
 *
 * @param {array} props - array with the props that should be augmented
 */
export function responsive(propNames) {
	// For testing, return the actual class instead of wrapping it
	if (typeof process === 'object' && process.env.NODE_ENV === 'testing') {
		return DecoratedClass => DecoratedClass;
	}

	return DecoratedClass =>
		class extends Component {
			constructor(props) {
				super(props);
				this.throttledOnResize = throttle(this.onResize.bind(this), 100, {
					leading: false
				});
			}

			onResize() {
				this.forceUpdate();
			}

			componentDidMount() {
				this.onResize();
				window.addEventListener('resize', this.throttledOnResize);
			}

			componentWillUnmount() {
				window.removeEventListener('resize', this.throttledOnResize);
			}

			render() {
				const newProps = Object.assign({}, this.props);
				propNames.forEach(propName => {
					newProps[propName] = assignValueByBreakpoint(this.props[propName]);
				});
				return <DecoratedClass {...newProps} />;
			}
		};
}

export default responsive;



