import React, { Component } from 'react';
import PropTypes from 'prop-types';
import styles from './Autocomplete.css';
import theme from '../common/theme';
import Icon from '../icon/Icon';

export const stripHTML = value => {
	const div = document.createElement('div');
	div.innerHTML = value;
	return div.textContent || div.innerText || '';
};

@theme(styles)
export default class Autocomplete extends Component {
	constructor(props) {
		super(props);
		this.state = {
			showAutocomplete: true,
			autocompleteItems: props.autocompleteItems
		};

		this.onAutocompleteClickHandler = this.onAutocompleteClickHandler.bind(this);
	}

	static getDerivedStateFromProps(props, state) {
		try {
			if (props.autocompleteItems[0] !== state.autocompleteItems[0]) {
				return {
					showAutocomplete: true,
					autocompleteItems: props.autocompleteItems
				};
			}
			return null;
		} catch (error) {
			return null;
		}
	}

	onAutocompleteClickHandler(item) {
		const { onAutocompleteClick } = this.props;
		onAutocompleteClick(stripHTML(item));
		this.setState({ showAutocomplete: false });
	}

	render() {
		const { autocompleteItems, css = styles } = this.props;
		const { showAutocomplete } = this.state;

		return (
			<span>
				{showAutocomplete &&
					autocompleteItems &&
					autocompleteItems.length > 0 && (
						<span>
							<Icon
								name="close-x"
								onClick={() => this.setState({ showAutocomplete: false })}
								extraClasses={{ icon: styles.closeIcon }}
								size={18}
							/>
							<ul className={css.autocompleteContainer}>
								{autocompleteItems.map((item, count) => {
									return (
										<li className={css.item} key={count}>
											<a
												onClick={this.onAutocompleteClickHandler.bind(null, item)}
												dangerouslySetInnerHTML={{ __html: item }}
											/>
										</li>
									);
								})}
							</ul>
						</span>
					)}
			</span>
		);
	}
}

Autocomplete.propTypes = {
	/**
	 * An array of autocomplete result items
	 */
	autocompleteItems: PropTypes.array,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * A callback function fired as the user clicks an autocomplete item
	 */
	onAutocompleteClick: PropTypes.func
};



