import React, { Component } from 'react';
import PropTypes from 'prop-types';

import styles from './TabGroup.css';
import theme from '../common/theme';

/**
A component to render a single tab.
*/
@theme(styles)
export class Tab extends Component {
	render() {
		const { active, css = styles, disabled, onClick, title } = this.props;

		return (
			<button className={`${css.tabButton} ${active ? css.active : ''}`} disabled={disabled} onClick={onClick}>
				{title}
			</button>
		);
	}
}

Tab.propTypes = {
	/**
	 * Determines if this tab is considered active
	 */
	active: PropTypes.bool,

	/**
	 * Content displayed inside this tab window
	 */
	children: PropTypes.node.isRequired,

	/**
	 * Determines if this tab is considered disabled
	 */
	disabled: PropTypes.bool,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Called when the tab is clicked
	 */
	onClick: PropTypes.func,

	/**
	 * String content to show inside this tab
	 */
	title: PropTypes.string
};

/**
Component that renders tab components in a horizontal layout.
*/
@theme(styles)
export class TabGroup extends Component {
	render() {
		const { activeTab, children, css = styles, onTabChange } = this.props;

		let activeContent;

		return (
			<div className={css.root}>
				<div className={css.tabButtons}>
					{children.map((tab, index) => {
						const tabProps = {
							key: index,
							onClick: () => onTabChange(index)
						};
						if (index === activeTab) {
							tabProps.active = true;
							activeContent = tab.props.children;
						}
						return React.cloneElement(tab, tabProps);
					})}
				</div>
				<div className={css.tabContent}>{activeContent}</div>
			</div>
		);
	}
}

export default TabGroup;

TabGroup.defaultProps = {
	activeTab: 1
};

TabGroup.propTypes = {
	/**
	 * The index of the currently active tab
	 */
	activeTab: PropTypes.number.isRequired,

	/**
	 * Tabs to display inside this component
	 */
	children: PropTypes.node,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.object,

	/**
	 * Called when the tab is changed; this should update the activeTab prop
	 */
	onTabChange: PropTypes.func.isRequired
};



