import React, { Component, Fragment } from 'react';
import PropTypes from 'prop-types';

import theme from '../common/theme';
import { Icon, IconColors } from '../icon/Icon';
import { Link } from '../link/Link';
import { Logo, LogoSizes } from '../logo/Logo';
import { SearchBar } from '../searchbar/SearchBar';

import styles from './Navigation.css';

@theme(styles)
export class Navigation extends Component {
	constructor(props) {
		super(props);

		this.toggleMenu = this.toggleMenu.bind(this);
		this.toggleSearch = this.toggleSearch.bind(this);

		this.state = {
			menu: false,
			search: false
		};
	}

	async toggleMenu() {
		await this.setState({ menu: !this.state.menu, search: false });
		document.body.style.overflowY = this.state.menu ? 'hidden' : 'auto';
	}

	toggleSearch() {
		this.setState({ search: !this.state.search, menu: false });
	}

	render() {
		const { css = styles, items, secondaryItems, slim, onSearch, onAutocomplete, onAutocompleteClick } = this.props;
		const { search, menu } = this.state;

		return (
			<div className={`${css.root} ${search ? css.search : ''} ${menu ? css.menu : ''} ${slim ? css.slim : ''}`}>
				<Logo size={LogoSizes.small} extraClasses={{ root: css.headerLogo }} />
				<div className={css.container}>
					<div
						className={css.safariLogo}
						dangerouslySetInnerHTML={{ __html: require('../common/images/safari-logo-red.svg') }}
					/>
					<div className={css.headerIcons}>
						<Icon
							color={IconColors.white}
							extraClasses={{ root: css.searchIcon }}
							name={search ? 'zoom-out' : 'search'}
							onClick={this.toggleSearch}
							size={24}
						/>
						<Icon
							color={IconColors.white}
							extraClasses={{ root: css.menuIcon }}
							name={menu ? 'close-x' : 'menu'}
							onClick={this.toggleMenu}
							size={24}
						/>
					</div>
					<div className={css.headerMenu}>
						<div className={css.headerLinks}>
							{items || (
								<Fragment>
									<Link icon="home" to="/home" key="home">
										Browse
									</Link>
									<Link icon="recommendation" to="/r" key="rec">
										Recommended
									</Link>
									<Link icon="collection" to="/s" key="collection">
										Queue
									</Link>
									<Link icon="history" to="/history" key="history">
										History
									</Link>
									<Link icon="topics" to="/topics" key="topics">
										Topics
									</Link>
									<Link icon="tutorials" to="/tutorials" key="tutorials">
										Tutorials
									</Link>
									<Link icon="event" to="/oreilly-conferences" key="event">
										Conferences
									</Link>
									<Link icon="document" to="/oreilly-newsletters" key="document">
										Newsletters
									</Link>
								</Fragment>
							)}
							<div className={css.secondary}>
								{secondaryItems || (
									<Fragment>
										<Link icon="settings" to="/u" key="settings">
											Settings
										</Link>
										<Link icon="edit-group" to="/public/support" key="edit-group">
											Support
										</Link>
										<Link icon="log-out" to="/accounts/logout" key="log-out">
											Sign Out
										</Link>
									</Fragment>
								)}
							</div>
						</div>
					</div>
					<div className={css.searchContainer}>
						<SearchBar
							ariaHidden={true}
							extraClasses={{
								root: css.searchBarRoot,
								inputRoot: css.searchInput,
								action: css.searchAction,
								searchContainer: css.searchBox,
								filterContainer: css.filterBox
							}}
							filterLabel="Filter by"
							filterOptions={[
								{ label: 'All', value: 'all' },
								{ label: 'Title', value: 'title' },
								{ label: 'Author', value: 'authors' },
								{ label: 'Publisher', value: 'publishers' }
							]}
							inputId="search"
							inputLabel="Search"
							onSearch={onSearch}
							onAutocomplete={onAutocomplete}
							onAutocompleteClick={onAutocompleteClick}
							placeholder="Search Safari..."
							variant={slim ? 'slim' : ''}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default Navigation;

Navigation.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Items displayed inside this navigation bar
	 */
	items: PropTypes.node,

	/**
	 * A callback function fired as the user enters text
	 */
	onAutocomplete: PropTypes.func,

	/**
	 * A callback function fired as the user clicks an autocomplete item
	 */
	onAutocompleteClick: PropTypes.func,

	/**
	 * Called with the current query when a user triggers a search
	 */
	onSearch: PropTypes.func,

	/**
	 * Minor items displayed inside this navigation bar
	 */
	secondaryItems: PropTypes.node,

	/**
	 * Determines if this component should render in slim mode
	 */
	slim: PropTypes.bool
};



