import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Icon from '../icon/Icon';
import theme from '../common/theme';

import styles from './Notification.css';

@theme(styles)
export class Notification extends Component {
	render() {
		const { children, css = styles, icon, onClose, open } = this.props;

		if (!open) {
			return null;
		}

		return (
			<div className={css.root}>
				{icon && (
					<div className={css.iconContainer}>
						<div className={css.icon}>
							<Icon name={icon} size={{ xsmall: 16, xsmall600: 20, small960: 28 }} />
						</div>
					</div>
				)}
				<div className={css.content}>{children}</div>
				<div className={css.closeContainer}>
					<div className={css.close} onClick={onClose}>
						<Icon name="close-x" />
					</div>
				</div>
			</div>
		);
	}
}

export default Notification;

Notification.propTypes = {
	/**
	 * Content displayed inside the notification
	 */
	children: PropTypes.node.isRequired,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Name of an icon to display
	 */
	icon: PropTypes.string,

	/**
	 * Called when the close button is clicked
	 */
	onClose: PropTypes.func,

	/**
	 * Determines if this notification is visible
	 */
	open: PropTypes.bool
};



