import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Icon from '../icon/Icon';
import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';

import styles from './IconLabel.css';

export const IconLabelColors = {
	red: 'red',
	charged: 'charged',
	iron: 'iron',
	white: 'white'
};

export const IconLabelSizes = {
	small: 'small',
	large: 'large'
};

const IconSizes = {
	small: 16,
	large: 24
};

@responsive(['size'])
@theme(styles)
export class IconLabel extends Component {
	render() {
		const { css = styles, icon, label, color, size, onClick } = this.props;
		return (
			<div className={`${css.root} ${size ? css[size] : ''} ${color ? css[color] : ''}`} onClick={onClick}>
				<Icon name={icon} size={size ? IconSizes[size] : 20} extraClasses={{ icon: css.icon }} />
				<span className={css.label}>{label}</span>
			</div>
		);
	}
}

export default IconLabel;

IconLabel.propTypes = {
	/**
	 * IconLabel color
	 */
	color: PropTypes.oneOf(Object.values(IconLabelColors)),

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Icon name
	 */
	icon: PropTypes.string.isRequired,

	/**
	 * Label
	 */
	label: PropTypes.string.isRequired,

	/**
	 * Called when this label is clicked
	 */
	onClick: PropTypes.func,

	/**
	 * IconLabel size
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.values(IconLabelSizes)))
};



