import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import theme from '../common/theme';

import styles from './ProgressBar.css';

export const ProgressBarPosition = {
	before: 'before',
	after: 'after'
};

export const ProgressBarVariants = {
	slim: 'slim'
};

@theme(styles)
export class ProgressBar extends Component {
	render() {
		const { css = styles, progress, variant, labels, inactive, labelsPosition } = this.props;
		const labelsArray = [].concat(labels);
		const labelsString = labels ? <div className={css.labels}>{labelsArray.join(' • ')}</div> : null;

		return (
			<div
				className={classnames(css.root, css[variant], css[`labels-${labelsPosition}`], {
					[css.inactive]: inactive
				})}>
				{labelsPosition === ProgressBarPosition.before && labelsString}
				<div className={css.bar}>
					<div className={css.progress} style={{ width: `${progress}%` }} />
				</div>
				{labelsPosition === ProgressBarPosition.after && labelsString}
			</div>
		);
	}
}

export default ProgressBar;

ProgressBar.defaultProps = {
	labelsPosition: ProgressBarPosition.after
};

ProgressBar.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Determines if this progress bar renders in an inactive state
	 */
	inactive: PropTypes.bool,

	/**
	 * Position of labels. Defaults to 'before'
	 */
	labelsPosition: PropTypes.oneOf(Object.values(ProgressBarPosition)),

	/**
	 * A string or array of strings with labels to display in the progress bar
	 */
	labels: PropTypes.oneOfType([PropTypes.string, PropTypes.arrayOf(PropTypes.string)]),

	/**
	 * Number between 0 and 100 indicating progress
	 */
	progress: PropTypes.number,

	/**
	 * Variant of progress bar
	 */
	variant: PropTypes.oneOf(Object.values(ProgressBarVariants))
};



