/* eslint react/prop-types: 0 */
import React, { Component } from 'react';

/**
 * ES class decorator to shuffle a CSS module to a `css` React prop
 * @param {object} css - imported CSS module
 */
export function theme(css) {
	// For testing, return the actual class instead of wrapping it
	if (typeof process === 'object' && process.env.NODE_ENV === 'testing') {
		return DecoratedClass => DecoratedClass;
	}

	return DecoratedClass =>
		class extends Component {
			render() {
				const { extraClasses = {} } = this.props;
				const newCss = Object.assign({}, css);

				for (const className in newCss) {
					if (extraClasses[className]) {
						newCss[className] = `${newCss[className]} ${extraClasses[className]} `;
					}
				}

				return <DecoratedClass {...this.props} css={newCss} />;
			}
		};
}

export default theme;



