import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import Icon from '../icon/Icon';
import theme from '../common/theme';

import styles from './Pagination.css';

export const PaginationSizes = {
	large: 'large'
};

export const PaginationVariants = {
	block: 'block'
};

@theme(styles)
export class Pagination extends Component {
	render() {
		const { css = styles, label, size, variant, disabled, next, previous, onClick } = this.props;
		return (
			<a
				onClick={disabled ? null : onClick}
				className={classnames(css.root, css[variant], {
					[css[size]]: size,
					[css.disabled]: disabled,
					[css.previous]: previous,
					[css.next]: next
				})}>
				{next && label && <span className={css.label}>{label}</span>}
				<Icon
					name={`chevron-${previous ? 'left' : 'right'}`}
					size={size == PaginationSizes.large ? 24 : 16}
					extraClasses={{ icon: css.icon }}
				/>
				{previous && label && <span className={css.label}>{label}</span>}
			</a>
		);
	}
}

export default Pagination;

Pagination.propTypes = {
	/**
	 * Disable Pagination button
	 */
	disabled: PropTypes.bool,

	/**
	 * Extra classes for the button
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Pagination label
	 */
	label: PropTypes.string,

	/**
	 * Next page
	 */
	next: PropTypes.bool,

	/**
	 * Callback
	 */
	onClick: PropTypes.func.isRequired,

	/**
	 * Previous page
	 */
	previous: PropTypes.bool,

	/**
	 * Pagination size
	 */
	size: PropTypes.oneOf(Object.keys(PaginationSizes)),

	/**
	 * Pagination variant
	 */
	variant: PropTypes.oneOf(Object.keys(PaginationVariants))
};



