import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import Truncate from '../truncate/Truncate';
import theme from '../common/theme';
import { ResponsivePropType } from '../common/responsive';

import styles from './ByLine.css';

@theme(styles)
export class ByLine extends Component {
	renderAuthors() {
		const { authors, onClick, css = styles } = this.props;

		return [].concat(
			'By ',
			authors
				.map((author, idx) => {
					return (
						<span key={idx} onClick={() => onClick && onClick(author)} className={css.author}>
							{author}
						</span>
					);
				})
				.reduce((prev, next, idx) => {
					return prev.concat([idx == 0 ? '' : idx == authors.length - 1 ? ' and ' : ', ', next]);
				}, []),
			'.'
		);
	}

	render() {
		const { css = styles, onClick, lines } = this.props;
		const rootClasses = classnames(css.root, {
			[css.interactive]: onClick
		});

		const authorsList = this.renderAuthors();

		if (lines) {
			return (
				<Truncate lines={lines}>
					<p className={rootClasses}>{authorsList}</p>
				</Truncate>
			);
		}
		return <p className={rootClasses}>{authorsList}</p>;
	}
}

export default ByLine;

ByLine.propTypes = {
	/**
	 * Array of authors to render
	 */
	authors: PropTypes.arrayOf(PropTypes.string).isRequired,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Amount of lines to truncate
	 */
	lines: ResponsivePropType(PropTypes.number),

	/**
	 * Called when an author is clicked
	 */
	onClick: PropTypes.func
};



