import React, { Component } from 'react';
import PropTypes from 'prop-types';

import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';

import styles from './Logo.css';

export const LogoSizes = {
	small: 'small',
	medium: 'medium'
};

@responsive(['size'])
@theme(styles)
export class Logo extends Component {
	render() {
		const { css = styles, size, onClick } = this.props;
		return (
			<span
				onClick={onClick}
				className={`${css.root} ${size ? css[size] : null}`}
				dangerouslySetInnerHTML={{ __html: require('../common/images/logo-white.svg') }}
			/>
		);
	}
}

export default Logo;

Logo.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Called when this logo is clicked
	 */
	onClick: PropTypes.func,

	/**
	 * Size of logo
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.keys(LogoSizes)))
};



