import React, { Component } from 'react';
import PropTypes from 'prop-types';
import TruncateMarkup from 'react-truncate-markup';

import responsive, { ResponsivePropType } from '../common/responsive';

@responsive(['lines'])
export class Truncate extends Component {
	render() {
		const { lines = 3, children } = this.props;
		return <TruncateMarkup lines={lines}>{children}</TruncateMarkup>;
	}
}

export default Truncate;

Truncate.propTypes = {
	/**
	 * Content to be truncated
	 */
	children: PropTypes.node.isRequired,

	/**
	 * Number of lines before it truncates
	 */
	lines: ResponsivePropType(PropTypes.number),

	/**
	 * Tag to use. Defaults to h4
	 */
	tag: PropTypes.string
};



