import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Icon from '../icon/Icon';
import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';

import styles from './Toggle.css';

export const ToggleVariants = {
	switch: 'switch',
	pill: 'pill'
};

@responsive(['variant'])
@theme(styles)
export class Toggle extends Component {
	constructor(props) {
		super(props);
		this.state = { focus: false };
		this.handleOnChange = this.handleOnChange.bind(this);
		this.handleOnFocus = this.handleOnFocus.bind(this);
		this.handleOnBlur = this.handleOnBlur.bind(this);
	}

	handleOnChange(event) {
		const { name, checked } = event.target;
		const { value = true, onChange } = this.props;
		const returnValue = checked ? value : false;
		onChange && onChange(event, name, returnValue);
	}

	handleOnFocus() {
		this.setState({ focus: true });
	}

	handleOnBlur() {
		this.setState({ focus: false });
	}

	render() {
		const { checked, css = styles, name, variant, offLabel, onLabel, offIcon, onIcon } = this.props;

		return (
			<div
				className={`${css.root} ${checked ? css.checked : ''} ${css[variant]} ${
					this.state.focus ? css.focus : ''
				}`}>
				<div className={`${css.label} ${css.off}`}>
					{offIcon && <Icon name={offIcon} extraClasses={{ icon: css.icon }} />}
					{offLabel}
				</div>
				<div className={`${css.label} ${css.on}`}>
					{onIcon && <Icon name={onIcon} extraClasses={{ icon: css.icon }} />}
					{onLabel}
				</div>
				<span className={css.selection} />
				<input
					type="checkbox"
					name={name}
					checked={checked}
					onChange={this.handleOnChange}
					onFocus={this.handleOnFocus}
					onBlur={this.handleOnBlur}
				/>
			</div>
		);
	}
}

export default Toggle;

Toggle.defaultProps = {
	onLabel: 'On',
	offLabel: 'Off',
	variant: ToggleVariants.switch
};

Toggle.propTypes = {
	/**
	 * Whether the checkbox is currently checked
	 */
	checked: PropTypes.bool,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * The name of the checkbox
	 */
	name: PropTypes.string,

	/**
	 * Text to display for the off position
	 */
	offLabel: PropTypes.string,

	/**
	 * Optional icon for the off position
	 */
	offIcon: PropTypes.string,

	/**
	 * Called when this checkbox is clicked
	 */
	onChange: PropTypes.func,

	/**
	 * Optional icon for the on position
	 */
	onIcon: PropTypes.string,

	/**
	 * Text to display for the on position
	 */
	onLabel: PropTypes.string,

	/**
	 * The value associated with this toggle
	 */
	value: PropTypes.string,

	/**
	 * Variant of switch to display
	 */
	variant: ResponsivePropType(PropTypes.oneOf(Object.keys(ToggleVariants)))
};



