import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import styles from './Radio.css';
import { uid } from '../common/util';
import theme from '../common/theme';
import responsive, { ResponsivePropType } from '../common/responsive';

export const RadioSizes = {
	small: 'small',
	large: 'large'
};

@responsive(['size'])
@theme(styles)
export class Radio extends Component {
	constructor(props) {
		super(props);
		this.state = { focus: false };
		this._id = uid('radio');
		this.handleOnChange = this.handleOnChange.bind(this);
		this.handleOnFocus = this.handleOnFocus.bind(this);
		this.handleOnBlur = this.handleOnBlur.bind(this);
	}

	handleOnChange(event) {
		const { onChange } = this.props;
		const { name, value } = event.target;
		onChange && onChange(event, name, value);
	}
	handleOnFocus() {
		this.setState({ focus: true });
	}

	handleOnBlur() {
		this.setState({ focus: false });
	}

	render() {
		const { checked, css = styles, disabled, id = this._id, name, size, value, label } = this.props;
		return (
			<div
				className={classnames(css.root, {
					[css[size]]: size,
					[css.focus]: this.state.focus,
					[css.checked]: checked,
					[css.disabled]: disabled
				})}>
				<div className={css.radio}>
					{checked && <div className={css.innerCircle} />}
					<input
						checked={checked}
						id={id}
						name={name}
						onChange={this.handleOnChange}
						onFocus={this.handleOnFocus}
						onBlur={this.handleOnBlur}
						type="radio"
						value={value}
					/>
				</div>
				{label && (
					<label className={css.label} htmlFor={id}>
						{label}
					</label>
				)}
			</div>
		);
	}
}

export default Radio;

Radio.propTypes = {
	/**
	 * Whether the radio is currently checked
	 */
	checked: PropTypes.bool,

	/**
	 * Determines if this radio is disabled
	 */
	disabled: PropTypes.bool,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * String id to set on this radio, useful for labeling
	 */
	id: PropTypes.string,

	/**
	 * A label for the radio option
	 */
	label: PropTypes.string,

	/**
	 * Name to apply to the underlying input
	 */
	name: PropTypes.string,

	/**
	 * Called when this radio is clicked
	 */
	onChange: PropTypes.func,

	/**
	 * Size of input
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.keys(RadioSizes))),

	/**
	 * Value to apply to the underlying input
	 */
	value: PropTypes.string
};



