import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Icons from '../common/styles/fonts/Icons/Icons.json';
import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';

import styles from './Icon.css';

export { Icons };

export const IconColors = {
	red: 'red',
	charged: 'charged',
	white: 'white',
	iron: 'iron'
};

export const IconSizes = (() => {
	const size = [];
	for (var i = 16; i <= 256; i += 4) {
		size.push(i);
	}
	return size;
})();

@responsive(['size'])
@theme(styles)
export class Icon extends Component {
	constructor(props) {
		super(props);
	}
	render() {
		const { css = styles, name, title, size, onClick, color, ariaHidden = false } = this.props;
		const icon = Icons[name];

		if (!icon) return null;

		return (
			<span
				className={css.root}
				aria-hidden={ariaHidden.toString()}
				style={{ height: `${size}px`, lineHeight: `${size}px` }}>
				<span
					style={{ fontSize: size, width: size, height: size }}
					className={`${css.icon} orm-icon-${name} ${color ? css[color] : ''}`}
					onClick={onClick}
					aria-hidden="true"
				/>
				<span className={css.title}>{title || name.replace(/-/g, ' ')}</span>
			</span>
		);
	}
}

export default Icon;

Icon.defaultProps = {
	size: 16
};

Icon.propTypes = {
	/**
	 * Used to prevent screen readers from announcing the icon
	 */
	ariaHidden: PropTypes.bool,

	/**
	 * Icon color
	 */
	color: PropTypes.oneOf(Object.values(IconColors)),

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Icon name
	 */
	name: PropTypes.oneOf(Object.keys(Icons)).isRequired,

	/**
	 * Called when this icon is clicked
	 */
	onClick: PropTypes.func,

	/**
	 * Icon size
	 */
	size: ResponsivePropType(PropTypes.oneOf(IconSizes)),

	/**
	 * Used to override the default visually-hidden Icon title that is announced by screen readers
	 */
	title: PropTypes.string
};



