import React, { Component } from 'react';
import PropTypes from 'prop-types';

import theme from '../common/theme';
import { Button, ButtonSizes, ButtonVariants } from '../button/Button';
import { Icon } from '../icon/Icon';
import { Link } from '../link/Link';
import { Logo, LogoSizes } from '../logo/Logo';
import { SearchBar, SearchBarVariants } from '../searchbar/SearchBar';

import styles from './AnonymousNavigation.css';

const TRIAL_PATH = '/public/free-trial/';

@theme(styles)
export class AnonymousNavigation extends Component {
	constructor(props) {
		super(props);

		this.toggleMenu = this.toggleMenu.bind(this);
		this.toggleSearch = this.toggleSearch.bind(this);

		this.state = {
			menu: false,
			search: false
		};
	}

	async toggleMenu() {
		await this.setState({ menu: !this.state.menu, search: false });
		document.body.style.overflowY = this.state.menu ? 'hidden' : 'auto';
	}

	toggleSearch() {
		this.setState({ search: !this.state.search, menu: false });
	}

	render() {
		const { css = styles, hideTrial, onSearch } = this.props;
		const { search, menu } = this.state;

		return (
			<div
				className={`${css.root} ${search ? css.search : ''} ${menu ? css.menu : ''} ${
					hideTrial ? css.noTrial : ''
				}`}>
				<Logo size={LogoSizes.small} extraClasses={{ root: css.headerLogo }} />
				<div className={css.container}>
					<div
						className={css.safariLogo}
						dangerouslySetInnerHTML={{ __html: require('../common/images/safari-logo-red.svg') }}
					/>
					{!hideTrial && (
						<Button
							onClick={
								/* istanbul ignore next */
								() => {
									window.location.href = `${window.location.origin}${TRIAL_PATH}`;
								}
							}
							extraClasses={{ root: css.headerTrialButton }}
							size={ButtonSizes.small}
							variant={ButtonVariants.secondary}>
							Free Trial
						</Button>
					)}
					<div className={css.headerIcons}>
						<Icon
							name={search ? 'zoom-out' : 'search'}
							size={24}
							extraClasses={{ root: css.searchIcon }}
							onClick={this.toggleSearch}
						/>
						<Icon
							name={menu ? 'close-x' : 'menu'}
							size={24}
							extraClasses={{ root: css.menuIcon }}
							onClick={this.toggleMenu}
						/>
					</div>
					<div className={css.headerMenu}>
						<div className={css.headerLinks}>
							<Link to="/enterprise">Enterprise</Link>
							<Link to="/pricing">Pricing</Link>
							<Link to="#sign-in">Sign In</Link>
						</div>
						{!hideTrial && (
							<Button
								onClick={
									/* istanbul ignore next */
									() => {
										window.location.href = `${window.location.origin}${TRIAL_PATH}`;
									}
								}
								extraClasses={{ root: css.menuTrialButton }}
								size={{ xsmall: ButtonSizes.large, small: ButtonSizes.small, small960: null }}
								variant={ButtonVariants.secondary}>
								Start Free Trial
							</Button>
						)}
					</div>
					<div className={css.searchContainer}>
						<SearchBar
							variant={SearchBarVariants.slim}
							extraClasses={{ inputRoot: css.searchInput, action: css.searchAction }}
							placeholder="Search Safari..."
							onSearch={onSearch}
						/>
					</div>
				</div>
			</div>
		);
	}
}

export default AnonymousNavigation;

AnonymousNavigation.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Hides the trial button
	 */
	hideTrial: PropTypes.bool,

	/**
	 * Children displayed inside this navigation bar
	 */
	items: PropTypes.node,

	/**
	 * Called with the current query when a user triggers a search
	 */
	onSearch: PropTypes.func
};



