import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Icon from '../icon/Icon';
import theme from '../common/theme';
import { Grid, Row, Column } from '../grid/Grid';

import styles from './AnonymousFooter.css';

const YEAR = new Date().getFullYear();

@theme(styles)
export class AnonymousFooter extends Component {
	render() {
		const { css = styles } = this.props;

		return (
			<footer role="contentinfo" className={css.root}>
				<Grid>
					<Row extraClasses={{ root: css.topRow }}>
						<Column col={{ xsmall: 2, xsmall600: 4, small: 4 }} extraClasses={{ root: css.textLinks }}>
							<a href="/about">Our Company</a>
							<a href="/work-with-us">Teach / Speak / Write</a>
							<a href="/press">Press / Media Inquiries</a>
						</Column>
						<Column col={{ xsmall: 2, xsmall600: 4, small: 4 }} extraClasses={{ root: css.textLinks }}>
							<a href="/careers">Careers</a>
							<a href="/category/customer-service.do">Customer Service</a>
							<a href="/about/contact">Contact</a>
						</Column>
						<Column col={{ xsmall: 4, xsmall600: 8, small: 4 }} extraClasses={{ root: css.iconLinks }}>
							<a href="http://twitter.com/oreillymedia">
								<Icon name="twitter" size={{ xsmall: 24, small960: 28 }} />
							</a>
							<a href="http://twitter.com/oreillymedia">
								<Icon name="person" size={{ xsmall: 24, small960: 28 }} />
							</a>
							<a href="https://www.linkedin.com/company/oreilly-media">
								<Icon name="person" size={{ xsmall: 24, small960: 28 }} />
							</a>
							<br />
							<a href="https://www.youtube.com/user/OreillyMedia">
								<Icon name="share-post" size={{ xsmall: 24, small960: 28 }} />
							</a>
							<a href="http://www.oreilly.com/emails/newsletters/">
								<Icon name="email" size={{ xsmall: 24, small960: 28 }} />
							</a>
						</Column>
					</Row>
					<Row>
						<Column col={{ xsmall: 4, xsmall600: 8, small: 12 }}>
							<div className={css.copyright}>
								© {YEAR} O’Reilly Media, Inc. All trademarks and registered trademarks appearing on
								oreilly.com are the property of their respective owners.
							</div>
							<div className={css.smallLinks}>
								<a href="/terms/">Terms of Service</a>
								<a href="/privacy">Privacy Policy</a>
								<a href="/about/editorial_independence.html">Editorial Independence</a>
							</div>
						</Column>
					</Row>
				</Grid>
			</footer>
		);
	}
}

export default AnonymousFooter;

AnonymousFooter.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string)
};



