import React, { Component } from 'react';
import PropTypes from 'prop-types';
import classnames from 'classnames';

import Icon from '../icon/Icon';
import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';
import { uid } from '../common/util';
import styles from './Input.css';

export const InputSizes = {
	small: 'small',
	large: 'large'
};

@responsive(['size'])
@theme(styles)
export class Input extends Component {
	constructor(props) {
		super(props);
		this.state = {
			focus: false,
			hasValue: false
		};
		this._id = uid('input');
		this.handleOnChange = this.handleOnChange.bind(this);
		this.handleOnFocus = this.handleOnFocus.bind(this);
		this.handleOnBlur = this.handleOnBlur.bind(this);
	}

	handleOnChange(event) {
		const { name, value } = event.target;
		const { onChange } = this.props;
		onChange && onChange(event, name, value);
		this.setState({
			hasValue: Boolean(value)
		});
	}

	handleOnFocus() {
		this.setState({ focus: true });
	}

	handleOnBlur() {
		this.setState({
			focus: false
		});
	}

	render() {
		const {
			ariaHidden = false,
			css = styles,
			disabled,
			error,
			icon,
			id = this._id,
			invalid = false,
			label,
			name,
			onKeyPress,
			placeholder,
			required = false,
			size,
			style,
			type,
			value
		} = this.props;
		const { focus, hasValue } = this.state;
		const rootClasses = classnames(css.root, css[size], {
			[css.withIcon]: icon,
			[css.invalid]: invalid,
			[css.disabled]: disabled,
			[css.focus]: focus
		});

		const inputClasses = classnames(css.input, {
			[css.hasValue]: hasValue
		});

		return (
			<div style={style} className={rootClasses}>
				{label && (
					<label htmlFor={id} className={css.inputLabel}>
						{icon && (
							<Icon
								name={icon}
								extraClasses={{ icon: css.icon }}
								size={size === InputSizes.large ? 24 : 16}
								ariaHidden={ariaHidden}
							/>
						)}
						<input
							className={inputClasses}
							id={id}
							name={name}
							type={type}
							value={value}
							placeholder={placeholder}
							disabled={disabled}
							onFocus={this.handleOnFocus}
							onBlur={this.handleOnBlur}
							onChange={this.handleOnChange}
							onKeyPress={onKeyPress}
							aria-required={required}
							aria-describedby={`error-${id}`}
							aria-invalid={invalid}
						/>
						<span className={css.labelTxt}>{label}</span>
					</label>
				)}
				{invalid &&
					error && (
						<div className={css.errorWrapper}>
							<Icon
								name="warning-fill"
								extraClasses={{ icon: css.errorIcon }}
								size={16}
								ariaHidden={true}
							/>
							<span id={`error-${id}`} className={css.errorTxt} aria-live="polite">
								{error}
							</span>
						</div>
					)}
			</div>
		);
	}
}

export default Input;

Input.defaultProps = {
	type: 'text',
	onChange: () => {}
};

Input.propTypes = {
	/**
	 * Used to prevent screen readers from announcing the icon
	 */
	ariaHidden: PropTypes.bool,

	/**
	 * Determines if this input is disabled
	 */
	disabled: PropTypes.bool,

	/**
	 * Validation error message
	 */
	error: PropTypes.string,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Optional icon to show in this input
	 */
	icon: PropTypes.string,

	/**
	 * Input id attribute
	 */
	id: PropTypes.string,

	/**
	 * Determines if this input is invalid
	 */
	invalid: PropTypes.bool,

	/**
	 * Input label
	 */
	label: PropTypes.string.isRequired,

	/**
	 * Input name attribute. Fired in the onChange event
	 */
	name: PropTypes.string,

	/**
	 * Called when this input changed
	 */
	onChange: PropTypes.func,

	/**
	 * Called when key is pressed in this input
	 */
	onKeyPress: PropTypes.func,

	/**
	 * Placeholder text for input
	 */
	placeholder: PropTypes.string,

	/**
	 * Determines if this input is required
	 */
	required: PropTypes.bool,

	/**
	 * Size of input
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.keys(InputSizes))),

	/**
	 * Inline styles to append to the root node of this component
	 */
	style: PropTypes.object,

	/**
	 * Type of input
	 */
	type: PropTypes.string,

	/**
	 * Value of input
	 */
	value: PropTypes.string
};



