import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Button from '../button/Button';
import theme from '../common/theme';

import styles from './CallToAction.css';

@theme(styles)
export class CallToAction extends Component {
	render() {
		const { buttonText, css = styles, onButtonClick, text } = this.props;

		return (
			<div className={css.root}>
				<div className={css.text}>{text}</div>
				<div className={css.action}>
					<Button onClick={onButtonClick} icon="chevron-right">
						{buttonText}
					</Button>
				</div>
			</div>
		);
	}
}

export default CallToAction;

CallToAction.defaultProps = {
	buttonText: 'Start Free Trial',
	text: `With Safari, you learn the way you learn best. Get unlimited access to videos,
		live online training, learning paths, books, interactive tutorials, and more.`
};

CallToAction.propTypes = {
	/**
	 * Label of the button for this call to action
	 */
	buttonText: PropTypes.string,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * A callback function fired when the user clicks the action button
	 */
	onButtonClick: PropTypes.func,

	/**
	 * Prompt to show above the button
	 */
	text: PropTypes.string
};



