import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Autocomplete from '../autocomplete/Autocomplete';
import Button, { ButtonSizes } from '../button/Button';
import Input, { InputSizes } from '../input/Input';
import Select, { SelectSizes } from '../select/Select';
import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';

import styles from './SearchBar.css';

export const SearchBarVariants = {
	slim: 'slim'
};

@responsive(['variant'])
@theme(styles)
export class SearchBar extends Component {
	constructor(props) {
		super(props);
		this.state = { filter: '', value: '', autocompleteItems: [] };

		this.handleKeyPress = this.handleKeyPress.bind(this);
		this.handleOnChange = this.handleOnChange.bind(this);
	}

	handleOnChange(evt) {
		const { onAutocomplete } = this.props;
		const { filter } = this.state;

		this.setState({ value: evt.target.value }, () => {
			if (onAutocomplete) {
				const response = onAutocomplete.call(null, this.state.value, filter);
				if (response && response.then) {
					response
						.then(response => {
							this.setState({ autocompleteItems: response });
						})
						.catch(err => {
							throw new Error(err);
						});
				} else {
					this.setState({ autocompleteItems: response });
				}
			}
		});
	}

	handleKeyPress(evt) {
		const { onSearch } = this.props;
		const { filter, value } = this.state;

		if (evt.which === 13) {
			if (onSearch) {
				onSearch.call(null, value, filter);
			}
		}
	}

	render() {
		const {
			ariaHidden,
			buttonText,
			css = styles,
			filterLabel,
			filterOptions,
			inputLabel,
			onSearch,
			onAutocompleteClick,
			placeholder,
			variant
		} = this.props;
		const { filter, value, autocompleteItems } = this.state;

		return (
			<div className={`${css.root} ${variant ? css[variant] : ''}`}>
				<div className={css.searchContainer}>
					<Input
						ariaHidden={ariaHidden}
						extraClasses={{ root: css.inputRoot, input: css.input }}
						icon="search"
						label={inputLabel}
						name="value"
						onChange={evt => this.handleOnChange(evt)}
						onKeyPress={evt => this.handleKeyPress(evt)}
						placeholder={placeholder}
						size={variant === SearchBarVariants.slim ? {} : InputSizes.large}
						type="search"
						value={this.state.value}
					/>
					<Autocomplete autocompleteItems={autocompleteItems} onAutocompleteClick={onAutocompleteClick} />
				</div>
				{filterOptions && (
					<div className={css.filterContainer}>
						<Select
							extraClasses={{ root: css.select }}
							label={filterLabel}
							name="filter"
							onChange={(e, name, value) => this.setState({ filter: value })}
							placeholder="Select..."
							size={variant === SearchBarVariants.slim ? {} : SelectSizes.large}>
							{filterOptions.map(option => (
								<option key={option.value} value={option.value}>
									{option.label || option.value}
								</option>
							))}
						</Select>
					</div>
				)}
				<div className={css.action}>
					<Button
						size={variant === SearchBarVariants.slim ? {} : ButtonSizes.large}
						onClick={() => onSearch && onSearch(value, filter)}>
						{buttonText}
					</Button>
				</div>
			</div>
		);
	}
}

export default SearchBar;

SearchBar.defaultProps = {
	buttonText: 'Search',
	inputLabel: 'Search',
	placeholder: 'Find a Solution...'
};

SearchBar.propTypes = {
	/**
	 * Used to prevent screen readers from announcing the icon
	 */
	ariaHidden: PropTypes.bool,

	/**
	 * Label of the button for this search bar
	 */
	buttonText: PropTypes.string,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Label to display inside the filter option select
	 */
	filterLabel: PropTypes.string,

	/**
	 * Options to show inside a Select box
	 */
	filterOptions: PropTypes.arrayOf(
		PropTypes.shape({
			label: PropTypes.string,
			value: PropTypes.oneOfType([PropTypes.string, PropTypes.number])
		})
	),

	/**
	 * Input label
	 */
	inputLabel: PropTypes.string.isRequired,

	/**
	 * A callback function fired when the user clicks the action button
	 */
	onSearch: PropTypes.func,

	/**
	 * A callback function fired as the user enters text
	 */
	onAutocomplete: PropTypes.func,

	/**
	 * A callback function fired as the user clicks an autocomplete item
	 */
	onAutocompleteClick: PropTypes.func,

	/**
	 * Placeholder text to display inside the text input
	 */
	placeholder: PropTypes.string,

	/**
	 * Determines the variant of this search bar
	 */
	variant: ResponsivePropType(PropTypes.oneOf(Object.keys(SearchBarVariants)))
};



