import React, { Component } from 'react';
import PropTypes from 'prop-types';

import responsive, { ResponsivePropType } from '../common/responsive';
import theme from '../common/theme';

import styles from './Avatar.css';

export const AvatarVariants = {
	featured: 'featured',
	inactive: 'inactive'
};

export const AvatarSizes = {
	xsmall: 'xsmall',
	small: 'small',
	large: 'large',
	xlarge: 'xlarge'
};

@responsive(['size', 'variant'])
@theme(styles)
export class Avatar extends Component {
	render() {
		const { css = styles, inactiveText, size, src, variant } = this.props;
		return (
			<div
				className={`${css.root} ${size ? css[size] : ''} ${variant ? css[variant] : ''}`}
				style={{ backgroundImage: `url(${src})` }}>
				{variant === AvatarVariants.inactive && <div className={css.tag}>{inactiveText}</div>}
			</div>
		);
	}
}

export default Avatar;

Avatar.defaultProps = {
	inactiveText: ''
};

Avatar.propTypes = {
	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Text to display if this avatar is inactive
	 */
	inactiveText: PropTypes.string,

	/**
	 * Size this avatar should assume
	 */
	size: ResponsivePropType(PropTypes.oneOf(Object.keys(AvatarSizes))),

	/**
	 * Source of an image file to display
	 */
	src: PropTypes.string.isRequired,

	/**
	 * Variant in which this avatar should display
	 */
	variant: ResponsivePropType(PropTypes.oneOf(Object.keys(AvatarVariants)))
};



