import React, { Component } from 'react';
import PropTypes from 'prop-types';

import Icon from '../icon/Icon';
import theme from '../common/theme';

import styles from './Modal.css';

@theme(styles)
export class Modal extends Component {
	render() {
		const { children, css = styles, fixed, onClose, open } = this.props;

		if (!open) {
			return null;
		}

		return (
			<div className={`${css.root} ${fixed ? css.fixed : ''}`}>
				{fixed && <div className={css.underlay} />}
				<div className={css.content}>
					<div className={css.close} onClick={onClose}>
						<Icon name="close-x" />
					</div>
					{children}
				</div>
			</div>
		);
	}
}

export default Modal;

Modal.propTypes = {
	/**
	 * Content displayed inside the anchor
	 */
	children: PropTypes.node.isRequired,

	/**
	 * A map of CSS classes to be appended to DOM nodes internally
	 */
	extraClasses: PropTypes.objectOf(PropTypes.string),

	/**
	 * Determines if this modal is fixed
	 */
	fixed: PropTypes.bool,

	/**
	 * Called when this modal should close
	 */
	onClose: PropTypes.func,

	/**
	 * Determines if this modal is visible
	 */
	open: PropTypes.bool
};



